* {
  padding: 0;
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  box-sizing: border-box;
  --primary-color: #1466B6;
}

body {
  background-color: #EBEDF0;
}

h1 {
  color: var(--primary-color);
  margin-bottom: 1em;
  font-size: 2em;
}

.bold {
  font-weight: bold;
  color: var(--primary-color);
}

